*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  min-width: 1170px;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Fira Sans', 'Arial', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 170%;
  text-align: center;
  color: #484848; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: 0;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.btn {
  border: 1px solid transparent;
  vertical-align: middle; }

@font-face {
  font-family: "Fira Sans";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: local("Fira Sans");
  src: url("../fonts/FiraSans-Regular.ttf") format("ttf"); }

@font-face {
  font-family: "Fira Sans";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Fira Sans");
  src: url("../fonts/FiraSans-Medium.ttf") format("ttf"); }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: local("Montserrat");
  src: url("../fonts/Montserrat-Bold.ttf") format("ttf"); }

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 800;
  font-display: swap;
  src: local("Raleway");
  src: url("../fonts/Raleway-ExtraBold.ttf") format("ttf"); }

